* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
  display: flex;
  align-items: flex-start;
  flex-direction: column;
  padding-left: 50px;
  padding-top: 50px;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

ul {
  list-style: none;
  padding: 0;
}

li {
  font-size: 18px;
  font-weight: 700;
}

img {
  display: block;
  margin-top: 30px;
  padding: 0;
}

.country-list li {
  display: flex;
  gap: 20px;
}

.country-info {
  display: flex;
  flex-direction: column;
}

.country-info p {
  font-size: 30px;
  display: inline-block;
}

.country-info li:not(:last-child) {
  margin-bottom: 20px;
}